<template>
    <div class="point" v-if="tableData">
        <div class="available-integral">
            <p>{{ $L('可用积分') }}</p>
            <p>{{ tableData.points }}</p>
            <router-link :to="{name:'integralList',params:{type:'all',points:'all'}}">
                <el-button type="primary" round size="small">{{ $L('积分兑换') }}</el-button>
            </router-link>
        </div>
        <div class="search" :class="{active:searchType}">
            <div class="search-box">
                <el-input :placeholder="$L('输入描述')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
            <div class="bbx-font bbx-icon-close mainFontColor" @click="showSearch(false)"></div>
        </div>
        <div class="search-status">
			<el-button size="small" class="time-search" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
            <el-select v-model="messageType" size="small"
                       @change="changeTypeFunc">
                <el-option
                        v-for="item in messageTypelist"
                        :key="item.type"
                        :label="item.name"
                        :value="item.type">
                </el-option>
            </el-select>
        </div>
        <div class="point-content">
            <div class="table">
                <div class="content" v-if="tableData && tableData.hasOwnProperty('list')" v-loading="loading">
                    <el-table
                            ref="multipleTable"
                            :data="tableData.list"
                            style="width: 100%"
                            class="table"
                            :default-sort="{prop:'log_id',order:'descending'}"
                            @selection-change="handleSelectionChange"
                            @sort-change="sortChange">
                        <!-- 复选框 -->
                        <el-table-column type="selection" width="40" align="left"></el-table-column>
                        <el-table-column width="210">
                            <div slot-scope="scope" class="point-info">
                                <p v-if="scope.row.orderSn" class="font-grey">{{ scope.row.orderSn }}</p>
                                <p class="point-memo">{{textType(scope.row.type,scope.row.point,scope.row.desc).textTwo}}<br></p>
                                <p class="point-desc">{{$L('描述')}}：{{textType(scope.row.type,scope.row.point,scope.row.desc).textTwo}}</p>
                                <p class="point-timer">{{scope.row.createTime}}</p>
                            </div>
                        </el-table-column>

                        <el-table-column align="right">
                            <div slot-scope="scope" class="point-info-two">
                                <p class="font-green">{{ scope.row.point }}</p>
                            </div>
                        </el-table-column>
                    </el-table>
                    <div class="list-pages" v-if="tableData.allNum>pageSize">
                        <el-pagination
                                small
                                background
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :page-size="pageSize"
                                layout="prev, pager, next"
                                :total="tableData.allNum">
                        </el-pagination>
                    </div>
                    <div class="botton-btn">
                        <div class="left">
                            <el-checkbox v-model="checkAll" @change="handleCheckAllChange(tableData.list)">{{ $L('全选') }}</el-checkbox>

                        </div>
                        <div class="right">
                            <el-button class="btn" type="primary" :disabled=disabled  @click="delMsgFunc()">{{ $L('删除') }}</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
		<transition name="fade">
			<div class="time" v-if="showTime">
				<div class="close" @click="closeTimeFunc"></div>
				<div class="date">
					<calendar></calendar>
                    <inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
                                    :weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
                                    @change="onChange" :defaultDate="defaultDate"></inlineCalendar>
					<div class="btns">
						<el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
						<el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
					</div>
				</div>
			</div>
		</transition>
    </div>
</template>

<script>
    export default {
        name: "myPoint",
        data() {
            return {
                loading: true,
                searchType: false,
                errorType: "error",
                errorTitle: this.$L("出错了"),
                errorTips: null,
                errorShow: false,
                pointNum: 0,
				disabled:true,
                messageTypelist: [
                    {
                        type: "all",
                        name: this.$L("全部积分")
                    },
                    {
                        type: "addPoint",
                        name: this.$L("增积分")
                    },
                    {
                        type: "cutPoint",
                        name: this.$L("减积分")
                    }
                ],
                messageType: "all",
                inputValue: "",
                sortBy: "",
                tableData: {},
                dtCreateStart: "",
                dtCreateEnd: "",
                pageSize: 10,
                currentPage: 1,
                msgArr: [],
                checkAll:false,
                allSelect: false,
				showTime:false,
				timeArr:[],
				defaultDate:[],
            };
        },
        computed: {
            //用于读取和设置顶部按钮
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 执行设置顶部按钮
            this.setupTopBtn();
            this.showSearch(false);
            next();
        },
        created() {
            //创建当前页面时执行设置顶部按钮
            this.setupTopBtn();
            this.getData();
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
        methods : {
            textType(status,point,desc){
                let x;
                switch (status) {
                    case 100:
                        x = {text: this.$L('积分购物'),textTwo: this.$L('积分购物')};
                        break;
                    case 101:
                        x = {text: this.$L('签到积分'),textTwo: this.$L('签到积分')+point};
                        break;
                    case 102:
                        x = {text: this.$L('会员登录积分'),textTwo: this.$L('会员登录积分')};
                        break;
                    case 104:
                        x = {text: this.$L('会员注册'),textTwo: this.$L('会员注册')};
                        break;
                    case 105:
                        x = {text: this.$L('关注微信公众号'),textTwo: this.$L('关注微信公众号')};
                        break;
                    case 106:
                        x = {text: this.$L('发展下级分销商'),textTwo: this.$L('发展下级分销商')};
                        break;
                    case 107:
                        x = {text: this.$L('发展下级会员'),textTwo: this.$L('发展下级会员')};
                        break;
                    case 201:
                        x = {text: this.$L('兑换产品'),textTwo: this.$L('积分商城产品兑换')};
                        break;
                    case 202:
                        x = {text: this.$L('兑换礼品'),textTwo: this.$L('积分商城礼品兑换')};
                        break;
                    case 203:
                        x = {text: this.$L('兑换代金券'),textTwo: this.$L('积分商城优惠劵兑换')};
                        break;
                    case 204:
                        x = {text: this.$L('积分抽奖'),textTwo: this.$L('积分商城积分抽奖')};
                        break;
                    case 205:
                        x = {text: this.$L('积分抵扣金额'),textTwo: this.$L('积分商城积分抵扣金额')};
                        break;
                    case 103:
                        x = {text: this.$L('会员卡获取积分'),textTwo: this.$L('会员卡获取积分')};
                        break;
                    case 108:
                        x = {text: this.$L('连续签到获取积分'),textTwo: this.$L('连续签到获取积分')+point};
                        break;
                    case 1:
                        x = {text: this.$L('管理员录入积分'),textTwo: this.$L('管理员录入积分')};
                        break;
                    case 3:
                        x = {text: this.$L('激活会员卡增加积分'),textTwo: this.$L('激活会员卡增加积分')+point};
                        break;
                    case 5:
                        x = {text: this.$L('初始化增加经验积分'),textTwo: this.$L('初始化增加经验积分')};
                        break;
                    case 0:
                        x = {text: this.$L('订单编号：没有订单信息'),textTwo: desc};
                        break;
                    default:
                        x = {text: this.$L('未知状态'),textTwo: this.$L('未知状态')};
                        break;
                }
                return x;
            },
            setupTopBtn() {
                this.mobileTopBtn = [
                    {
                        icon: 'bbx-icon-sousuo',
                        size: '24px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.showSearch(true);
                        }
                    }
                ];
            },
            showSearch(type) {
                if (type) {
                    //显示搜索框内容
                    this.searchType = true;
                } else {
                    //隐藏搜索框内容
                    this.searchType = false
                }
            },
            showError(title, tips, type) {
                if (type === "warning") {
                    this.errorType = "warning";
                } else {
                    this.errorType = "error";
                }
                if (title && title !== "") {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L("出错了");
                }
                if (tips && tips !== "") {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    order: _self.sortBy,
                    type: _self.messageType,
                    keyword: _self.inputValue,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd
                };
                _self.$api(
                    "getPointList",
                    arr,
                    function(reData) {
                        if (reData.flag === 1) {
                            _self.pointNum = reData.data.points;
                            _self.tableData = reData.data;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self
                                .$confirm(_self.$L("请重新登录"), _self.$L("登录超时"), {
                                    confirmButtonText: _self.$L("重新登录"),
                                    cancelButtonText: _self.$L("取消"),
                                    type: "error",
                                    distinguishCancelAndClose: true
                                })
                                .then(() => {
                                    _self.$router.push({
                                        name: "login",
                                        query: { redirect: _self.$route.fullPath }
                                    });
                                })
                                .catch(action => {
                                    _self.$gotoWebHome();
                                });
                        } else {
                            //未知错误
                            _self
                                .$confirm(_self.$L("数据加载失败"), _self.$L("出错了"), {
                                    confirmButtonText: _self.$L("点击重试"),
                                    cancelButtonText: _self.$L("返回首页"),
                                    type: "error",
                                    distinguishCancelAndClose: true
                                })
                                .then(() => {
                                    _self.getData();
                                })
                                .catch(action => {
                                    _self.$router.push({name: 'home'});
                                });
                        }
                    },
                    function(error) {
                        //网络请求错误
                        _self
                            .$confirm(_self.$L("网络请求失败"), _self.$L("出错了"), {
                                confirmButtonText: _self.$L("点击重试"),
                                cancelButtonText: _self.$L("返回首页"),
                                type: "error",
                                distinguishCancelAndClose: true
                            })
                            .then(() => {
                                _self.getData();
                            })
                            .catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                    }
                );
            },
            pointExchangegFunc() {
                this.$router.push({ name: "integralShop" });
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getData();
            },
            changeTypeFunc(val) {
                this.messageType = val;
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            SearchTimeFunc(time) {
                if (time !== null) {
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                } else {
                    this.dtCreateStart = "";
                    this.dtCreateEnd = "";
                }
                this.getData();
            },
            handleSelectionChange(val) {
				if(val.length > 0){
					this.disabled = false;
				}else{
					this.disabled = true;
				}
                let arr = [];
                for (let i in val) {
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            delMsgFunc(id) {
                const _self = this;
                if (!id && (Array.isArray(this.msgArr) && this.msgArr.length === 0)) {
                    //未知错误
                    _self
                        .$confirm(
                            _self.$L("您还没有选中积分记录，请勾选会员信息再做此操作"),
                            _self.$L("出错了"),
                            {
                                confirmButtonText: _self.$L("点击重试"),
                                cancelButtonText: _self.$L("返回首页"),
                                type: "warning",
                                distinguishCancelAndClose: true
                            }
                        )
                        .then(() => {
                            _self.getData();
                        })
                        .catch(action => {
                            if (action == 'cancel') {
                                _self.$router.push({name: 'pointList'});
                            }

                        });
                    return;
                }

                let groupId;

                if (id) {
                    groupId = id;
                } else {
                    groupId = this.msgArr;
                }

                let arr = {
                    id: groupId
                };
                _self
                    .$confirm(
                        _self.$L("此操作将永久删除该积分记录, 是否继续?"),
                        _self.$L("提示"),
                        {
                            confirmButtonText: _self.$L("确定"),
                            cancelButtonText: _self.$L("取消"),
                            type: "warning",
                            center: true
                        }
                    )
                    .then(() => {
                        _self.$api(
                            "deletePointMsg",
                            arr,
                            function(reData) {
                                if (reData.flag === 1) {
                                    _self.loading = false;
                                    _self.$message({
                                        type: "success",
                                        message: _self.$L("删除成功!")
                                    });
                                    _self.getData();
                                } else if (reData.flag === -1) {
                                    //登录超时了
                                    _self
                                        .$confirm(_self.$L("请重新登录"), _self.$L("登录超时"), {
                                            confirmButtonText: _self.$L("重新登录"),
                                            cancelButtonText: _self.$L("取消"),
                                            type: "error",
                                            distinguishCancelAndClose: true
                                        })
                                        .then(() => {
                                            _self.$router.push({
                                                name: "login",
                                                query: { redirect: _self.$route.fullPath }
                                            });
                                        })
                                        .catch(action => {
                                            _self.$gotoWebHome();
                                        });
                                } else {
                                    //未知错误
                                    _self
                                        .$confirm(_self.$L("数据加载失败"), _self.$L("出错了"), {
                                            confirmButtonText: _self.$L("点击重试"),
                                            cancelButtonText: _self.$L("返回首页"),
                                            type: "error",
                                            distinguishCancelAndClose: true
                                        })
                                        .then(() => {
                                            _self.getData();
                                        })
                                        .catch(action => {
                                            _self.$router.push({name: 'home'});
                                        });
                                }
                            },
                            function(error) {

                                //网络请求错误
                                _self
                                    .$confirm(_self.$L(error.msg), _self.$L("出错了"), {
                                        //todo:msg还未更换语言包语言包
                                        confirmButtonText: _self.$L("点击重试"),
                                        cancelButtonText: _self.$L("返回首页"),
                                        type: "error",
                                        distinguishCancelAndClose: true
                                    })
                                    .then(() => {
                                        _self.getData();
                                    })
                                    .catch(action => {
                                        _self.$router.push({name: 'home'});
                                    });
                            }
                        );
                    })
                    .catch(() => {});
            },
            sortChange(column, prop, order) {
                this.sortBy = column.prop;
                this.getData();
            },
            //全选
            handleCheckAllChange(rows){
                if (rows) {
                    rows.forEach(rows => {
                        this.$refs.multipleTable.toggleRowSelection(rows, !this.allSelect)
                    })
                    this.allSelect = !this.allSelect;

                    let arr = [];
                    if (this.allSelect) {
                        for (let i in rows){
                            arr[i] = rows[i].id;
                        }
                        this.msgArr = arr;
						this.disabled = false;
                    } else {
                        this.msgArr = arr;
						this.disabled = true;
                    }
                }
            },
			showTimeFunc(){
				this.showTime = !this.showTime;
			},
			onChange(date) {
				let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
				arr = arr.replace(/"|\[|\]/g,'');
				arr = arr.split(',');
				this.defaultDate = arr;
				this.dtCreateStart = arr[0];
				this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
			},
			closeTimeFunc(){
				this.showTime = false;
				this.defaultDate = [];
				this.dtCreateStart = '';
				this.dtCreateEnd = '';
				this.getData();
			},
			gotoTime(){
				this.showTime = false;
				this.getData();
			},
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";
    @import "../../../assets/css/common/lib.scss";

    .point {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        .search {
            position: relative;
            width: 100%;
            height: 0;
            line-height: 50px;
            text-align: center;
            overflow: hidden;
            @include diy-animation-show(height);
            border-bottom: 1px solid #F2F6FC;

            &.active {
                height: 50px;
                padding: 0 10px;
                background: #fff;
            }

            .bbx-font {
                position: absolute;
                width: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                top: 0;
                right: 0;
                font-size: 20px;
            }
            .search-box{
                width: calc(100% - 40px);
                /deep/ .el-input__inner{
                    padding: 0 10px;
                }
                /deep/ .el-input-group__append{
                    padding: 0 12px;
                    background: #fff;
                    color: #333;
                    font-size: 16px;
                }
            }
        }
        .search-status{
            height: 50px;
			line-height: 50px;
            border-bottom: 1px solid #F2F6FC;
            background: #fff;
			padding: 0 10px;
            /deep/ .el-select{
				margin-left: 10px;
                width: 100px;
                vertical-align: top;
            }
        }
        .available-integral {
            width: 100%;
            height: 180px;
            background-color: #0c89fd;
            text-align: center;
            color: #fff;
            padding: 10px 0;
            p:nth-of-type(1) {
                font-size: 14px;
                padding: 6px 0;
            }
            p:nth-of-type(2) {
                font-size: 31px;
                padding: 6px 0 20px;
            }
            .el-button {
                padding: 6px 36px;
                background-color: #fff;
                border-color: #fff;
                color: #0c89fd;
            }
        }
        .point-head {
            width: calc(100% - 200px);
            position: fixed;
            top: 50px;
            height: 50px;
            z-index: 999;
            margin: 0 -20px;
            background-color: #fff;
            .point-num {
                font-size: 18px;
                display: inline-block;
            }
            .right {
                float: left;
                margin-top: 7px;
                height: 35px;
            }
        }
        .point-content {
            margin: 10px 0 60px 0;
            .table-head {
                height: 32px;
                margin-bottom: 10px;
                .left {
                    float: left;
                }
                .right {
                    margin-left: 10px;
                    margin-top: 6px;
                    float: left;
                    .time-search {
                        vertical-align: bottom;
                    }
                    .font {
                        padding-left: 20px;
                        font-size: 16px;
                    }
                    .search {
                        width: 120px;
                        padding-left: 10px;
                    }
                    .input-with-select {
                        width: calc(100% - 400px);
                        padding-right: 5px;
                        margin-top: 10px;
                    }
                }
            }

            .table {
                background: #fff;
                /deep/ .el-table__header-wrapper{
                    display: none;
                }
                .font-grey {
                    color: #999;
                }
                .font-green {
                    color: #00bf00;
                }
                .content {
                    background: #fff;
                    .table {
                        padding: 0 10px;
                        .point-info{
                            p{
                                font-size: 10px;
                            }
                            .point-timer{
                                color: #999;
                            }
                            .point-desc{
                                font-size: 12px;
                                color: #999;
                            }
                        }
                        .point-info-two{
                            padding-right: 20px;
                            .point-desc{
                                font-size: 12px;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                                overflow: hidden;
                            }
                        }

                    }
                    .list-pages {
                        width: 100%;
                        text-align: center;
                        padding: 20px 0 20px;
                    }
                    .botton-btn{
                        position: fixed;
                        bottom: 0;
                        width: 100%;
                        height: 50px;
                        background: #fff;
                        z-index: 100;
                        border-top: 1px solid #F2F6FC;
                        .left{
                            display: inline-block;
                            line-height: 50px;
                            float: left;
                            padding-left: 25px;
                        }
                        .right{
                            display: inline-block;
                            height: 50px;
                            line-height: 50px;
                            float: right;
                            .btn{
                                height: inherit;
                                width: 120px;
                            }
                        }
                    }
                }
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #ffffff;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
		.time{
			height: 100%;
			position: fixed;
			z-index: 1000;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.6);
			.date{
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				.btns{
					background-color: #fff;
					text-align: right;
					padding: 5px 10px;
				}
			}
			.close{
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
			}
		}
		.fade-enter,
		.fade-leave-to{
		   opacity: 0;
		}
		.fade-enter-active,
		.fade-leave-active{
		   transition: opacity 0.5s
		}
    }
</style>
